body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.body{
    height: 100%;
    position: relative;
    width: 750px;
    margin-left: auto;
    margin-right: auto;
}
.header-container {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 300px;
    align-items: center;
    justify-content: space-between;
}

.footer{
    left: 0;
    bottom: 0;
    height: 100px;
    margin-top: 150px;
    margin-bottom: 50px;
    width: 100%;
}

@media screen and (max-width: 1170px) {
    .body{  
        width: 100%;
        height: 100%;
        padding: 20px;
    }
    .header-container{
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
    }
    .footer{
        left: 0;
        bottom: 0;
        height: 100px;
        margin-top: 50px;
        margin-bottom: 25px;
        width: 100%;
        padding: 20px;
    }
}
@font-face{
    src: url(/static/media/CircularStd-Medium.4fcdd97f.otf);
    font-family: circularstd;
}

@font-face{
    src: url(/static/media/Sen-Bold.f7e19def.ttf);
    font-family: sen-bold;
}

@font-face{
    src: url(/static/media/Sen-Regular.c92d05eb.ttf);
    font-family: sen-regular;
}

@font-face{
    src: url(/static/media/Now-SemiBold.54a4499a.otf);
    font-family: now-bold;
}




/* TITLE-Container*/
.title-container{
    display: grid;
    justify-items: left;
    grid-gap: 10px;
}

.name{
    font-family: circularstd;
    font-size: 25px;
    line-height: 20px;
}

.title{
    font-family: circularstd;
    font-size: 25px;
    line-height: 20px;
    
}

.goal{
    font-family: circularstd;
    font-size: 25px;
    line-height: 20px;
    color: orange;
}

.socials{
    display: flex;
    width: 100px;
    justify-content: space-evenly;
}

/* Navbar-Container*/
.navbar-container{
    display: flex;
    flex-direction: column;
    justify-items: left;
    grid-column-gap: 7.5px;
    -webkit-column-gap: 7.5px;
            column-gap: 7.5px;
}
.navButton{
    padding: 7.5px;
    line-height: 20px;
    background-color: transparent;
    font-size: 25px;
    font-family: circularstd;
    text-decoration: none;
    border: none;
    outline: none;
    cursor: pointer;
}
.navButton:after 
{
    opacity: 1.0;
    content: '';
    display: block;
    height: 3px;
    width: 0;
    transition: .5s;
    margin: auto; 
}
 
/* Change the width and background on hover, aka sliding from the middle to the left and right */
.navButton:hover:after 
{
    opacity: 1;
    width: 100%;
    background: orange;
}

.nav-active{
    opacity: 1.0;
    transition: 1s;
    -webkit-transform: translateX(25px);
            transform: translateX(25px);
} 

/* FOOTER CONTENTS */

.footer-text{
    font-family: circularstd;
    font-size: 17.5px;
    text-align: center;
    padding-bottom: 10px;
}

.footer-socials{
    line-height: 20px;
    display: flex;
    flex-direction: row;
    width: 150px;
    margin-left: auto;
    margin-right: auto;
    justify-content: space-evenly;
    margin-bottom: 20px;
}

.footer-bottom{
    color: grey;
    font-size: 12.5px;
    display: flex;
    flex-direction: row;
    width: 200px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    justify-content: space-evenly;
}


.footer-size{
    height: 25px;
    width: 25px;
}

@media screen and (max-width: 1170px) {
    .title-container{
        justify-items: center;
    }

    .navbar-container{
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
    }

    .nav-active{
        opacity: 1.0;
        transition: 1s;
        -webkit-transform: translateY(25px);
                transform: translateY(25px);
    } 
    
}
/* ABOUT PAGE */
