    font-family: circularstd;
    display: flex;
    flex-direction: column;
}

.projects-header{
    margin-top: 10px;
    font-size: 25px;
}

.projects-subheader{
    font-size: 20px;
}

/* Project 1*/
.projects-1-container{
    display: flex;
    flex-direction: row;
    width: 100%;
}
.projects-description{
    font-size: 20px;
    position: relative;
    flex-direction: end;
    text-align: right;
}
.projects-1{
    margin-top: 12.5px;
    position: relative;
    width: 50%;
    height: 200px;
}

.overlay{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: .5s ease;
    background-color: #DBCFC7;
}

/*#FFDBCFC7*/

.projects-1:hover .overlay{
    opacity: .9;
}

.projects-1-image{
    position: absolute;
    width: 100%;
    height: 100%;
}

.overlay-text{
    position: relative;
    color: white;
    height: 350px;
    font-size: 25px;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
}

